.weather {
  grid-template-areas: "icon temp properties";
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  align-items: center;
  margin-top: 28px;
  text-align: center;
}

.temp {
  grid-area: temp;
  justify-self: flex-start;
}

.temp h1 {
  margin: 0;
  font-weight: 300;
  font-size: 120px;
  font-family: "Roboto Mono", monospace;
  line-height: 1em;
}

.grid {
  display: grid;
}

.flex {
  display: flex;
}

.icon {
  grid-area: icon;
  justify-self: flex-end;
  max-height: 124px;
}

.properties {
  flex-direction: column;
  grid-area: properties;
  align-items: flex-start;
  font-family: "Roboto", sans-serif;
}

.properties__value {
  font-size: 27px;
}

.properties__value span {
  margin-left: 8px;
  font-size: 17px;
}
